.flex {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

body {
  font-family: 'Times New Roman', Times, serifW;
}

.backButton {
  position: absolute;
  top: 30px;
  left: 30px;
}

.contentMid {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex:before {
  background-image: url(https://1.bp.blogspot.com/-gxsOcYWghHA/Xp_izTh4sFI/AAAAAAAAU8s/y637Fwg99qAuzW9na_NT_uApny8Vce95gCEwYBhgL/s1600/header-footer-gradient-bg.png);
}

.flex:before {
  content: '';
    display: block;
    width: 100%;
    height: 4px;
    background-repeat: repeat-x;
    background-size: contain;
    position: absolute;
    top: 0;
    left: 0;
  opacity:0.5;
}

.stars {
  justify-self: start;
  position: absolute;
  width: 100vw;
