.contentForm {
  display: flex;
  justify-self: center;
  align-self: center;
}

.formTitle {
  text-align: center;
  font-weight: 1000;
}

.screen {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  gap: 2rem;
}

.form {
  width: 30rem;
  border-radius: 50px;
  background-color: #120329;
 

  padding: 4rem;
  backdrop-filter: blur(20px);
}

input:focus {
  box-shadow: 0 0 0 0;
  outline: 0;
  background: #371E5F;
  border: 1px solid orange;
}

input,
select {
  transition: all 0.5s ease-out;
  width: 100%;
  height: 2rem;
  text-align: center;
  background: #200B41;
  border: none;
  border-radius: 0.4rem;
  color: white;
  padding: 0 0 0 0.5rem;
  transition: all 0.5s ease-out; 
}

input:hover {
  filter: drop-shadow(8px 8px 9px #210D41)
}

.selectAluno {
  width: 100%;
  height: 2rem;
  text-align: center;
  background-color: #200B41;
  border: none;
  border-radius: 2rem;
  color: white;
  padding: 0 0 0 0.5rem;
  transition: background-color 0.5s ease-out; 
}

.contentMid {
  width: 100%;
}

.loginInput div {
  padding: 10px;

}

.p1 {
  color: white;
}

.button:hover, input:hover {
  background-color: #5819BB;
  transition: 0.5s;
}

.auth__error{
  padding: 10px 0;
  background-color: hsl(0, 79%, 87%);
  margin-bottom: 20px;
}

.cad {
  display: flex;
  flex-direction: row;
  gap: 5px;
}

.cad p {
  color: white;
}